.ui-box{
    margin: 0px !important;
    padding-top: 0px !important;
}

.tab-content{
    padding: 0;
}
.nav-tabs{
    background:#fff;
}
.select-width{
    max-width: 300px;
    width:100%;
    min-width: 150px;
}
.blanktr{
    height: 0;
    font-size: 0;
}
.blanktr td,
.blanktr th{
    margin: 0 !important;
    padding: 0 !important;
    font-size: 0 !important;
    border: none !important;
}
.blanktr th label{
    margin: 0 !important;
    padding: 0 !important;
    font-size: 0 !important;
}
.blanktr .sub-item{
    margin: 0 !important;
    padding: 0 !important;
    font-size: 0 !important;
}
.nav-tabs > li > a{
    border: 1px solid transparent !important;
}


.ui-body{
    height: 100%;
    overflow: hidden;
    .ui-full-page{
        height: 98.7%;
    }
    .ui-box{
        height: 100%;
        overflow: auto;
        .ui-body{
            height: calc(100% - 40px);
            overflow: auto;
        }
        .addPage{
            height: 100%;
            #noanim-tab-example{
                height: calc(100% - 33px);
                .tab-content{
                    position: absolute;
                    top: 34px;
                    width: 100%;
                    bottom: 0;
                    >div{
                        height: 100%;
                    }
                }
            }
        }
    }
}

.form-search-wrap{
    .tabcon{
        width: 100%;
        .sub-item{
            width: 100%;
            .tabComponent{
                width: 100%;
            }
        }
    }
}
.ext-tabcontent{
    .tab-content{
        position: static !important;
        margin-bottom: 8px;
    }
}


